body {
  background-color: #f1f1f1;
}
body .container {
  width: 80%;
  margin: 0 auto;
}
body header {
  background-color: #ffffff;
  box-shadow: 0px 4px 8px #0000001f;
  border-radius: 0px 0px 8px 8px;
}
body header section.quicklink-top {
  background-color: #f1f1f1;
}
body header section.quicklink-top .container {
  display: flex;
  justify-content: space-between;
  padding: 8px 0 10px 0;
}
@media (max-width: 768px) {
  body header section.quicklink-top .container {
    position: fixed;
    width: 100%;
    height: 0;
    overflow: hidden;
    padding: 0;
    top: 0;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
    transition: height 0.5s ease-in;
    background-color: #f1f1f1;
  }
}
body header section.quicklink-top .container .close-mobile {
  position: absolute;
  width: 100%;
  padding: 20px 0 20px 0;
  top: 0;
  z-index: 1;
  background-color: #cccccc;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  color: #999999;
  text-align: center;
}
@media (min-width: 769px) {
  body header section.quicklink-top .container .close-mobile {
    display: none;
  }
}
body header section.quicklink-top .container ul {
  display: flex;
}
@media (max-width: 768px) {
  body header section.quicklink-top .container ul {
    flex-direction: column;
    align-items: center;
  }
  body header section.quicklink-top .container ul:nth-child(2) {
    padding-top: 60px;
    order: 2;
  }
}
body header section.quicklink-top .container ul li {
  padding-right: 60px;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 0.75em;
  letter-spacing: 0.24px;
}
@media (max-width: 1024px) {
  body header section.quicklink-top .container ul li {
    padding-right: 16px;
    font-size: 0.7em;
  }
}
@media (max-width: 768px) {
  body header section.quicklink-top .container ul li {
    padding: 20px;
    font-family: "Open Sans", sans-serif;
    font-weight: 700;
  }
}
body header section.quicklink-top .container ul li .fa-phone {
  position: relative;
  top: 3px;
  padding-right: 5px;
  transform: rotate(45deg);
  color: #000000;
  font-size: 16px;
  font-weight: bold;
}
body header section.quicklink-top .container ul li .fa-whatsapp {
  position: relative;
  top: 1px;
  padding-right: 5px;
  color: #0dbc4f;
  font-size: 16px;
}
@media (min-width: 769px) {
  body header section.quicklink-top .container ul:nth-child(3) li {
    padding-top: 4px;
  }
}
@media (min-width: 769px) {
  body header section.quicklink-top .container ul:nth-child(3) li:last-child {
    padding-right: 0;
  }
}
body header section.quicklink-top .container-mobile {
  display: none;
}
@media (max-width: 768px) {
  body header section.quicklink-top .container-mobile {
    display: block;
    width: 80%;
    margin: 0 auto;
    padding-bottom: 5px;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu {
    position: relative;
    width: 60px;
    height: 60px;
    margin-left: -12px;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn {
    position: absolute;
    width: 60px;
    height: 60px;
    cursor: pointer;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn .btn-left {
    position: absolute;
    height: 8px;
    width: 15px;
    top: 30px;
    left: 15px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background-color: #333;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn .btn-left:before {
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: -12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background-color: #333;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn .btn-left:after {
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: 12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background-color: #333;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn .btn-right {
    position: absolute;
    height: 8px;
    width: 15px;
    top: 30px;
    left: 30px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background-color: #333;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn .btn-right:before {
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: -12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background-color: #333;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .btn .btn-right:after {
    position: absolute;
    width: 15px;
    height: 8px;
    content: "";
    top: 12px;
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background-color: #333;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .active .btn-left {
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background: transparent;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .active .btn-left:before {
    -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
    transform: rotateZ(45deg) scaleX(1.4) translate(4px, 4px);
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .active .btn-left:after {
    -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
    transform: rotateZ(-45deg) scaleX(1.4) translate(4px, -4px);
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .active .btn-right {
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    background: transparent;
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .active .btn-right:before {
    -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px);
    transform: rotateZ(-45deg) scaleX(1.4) translate(-4px, 4px);
  }
  body header section.quicklink-top .container-mobile .hamburguer-menu .active .btn-right:after {
    -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
    transform: rotateZ(45deg) scaleX(1.4) translate(-4px, -4px);
  }
}
body header section.quicklink-top .ul-quicklink-top-mobile {
  height: 100vh !important;
  transition: height 0.5s ease-in;
}
body header section.header-top .container {
  display: flex;
  justify-content: space-between;
  padding: 40px 0 40px 0;
}
@media (max-width: 768px) {
  body header section.header-top .container {
    height: 120px;
    padding-top: 10px;
  }
}
body header section.header-top .container .logo {
  width: 130px;
  height: 46px;
  margin: 6px 25px 0 0;
  background: url(/assets/img/logo.png);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 375px) {
  body header section.header-top .container .logo {
    width: 95px;
    height: 34px;
    margin-top: 13px;
  }
}
body header section.header-top .container .logo h1 {
  font-size: 2em;
  color: transparent;
}
body header section.header-top .container .search {
  width: 50%;
  height: 50px;
  margin: 0 auto;
  margin-right: 0;
  display: flex;
  box-shadow: 0px 0px 8px #00000029;
}
@media (max-width: 768px) {
  body header section.header-top .container .search {
    width: 80%;
    position: absolute;
    top: 150px;
  }
}
body header section.header-top .container .search input[type=text] {
  width: 82%;
  height: 50px;
  padding-right: 35px;
  padding-left: 20px;
  background: #ffffff;
  border: 2px solid #dfdfdf;
  border-radius: 5px;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 0.873em;
  color: #838282;
}
body header section.header-top .container .search button[type=submit] {
  width: 18%;
  height: 56px;
  margin-left: -40px;
  background: #ffffff;
  border-right: 2px solid #dfdfdf;
  border-top: 2px solid #dfdfdf;
  border-bottom: 2px solid #dfdfdf;
  border-radius: 0px 5px 5px 0px;
  cursor: pointer;
  color: #838282;
}
body header section.header-top .container .user-account {
  cursor: pointer;
  position: relative;
  width: 100px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px auto;
  padding-top: 6px;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 1em;
}
@media (min-width: 769px) {
  body header section.header-top .container .user-account:hover ul {
    visibility: visible;
    opacity: 1;
    transition: visibility 0.2s, opacity 0.2s linear;
  }
}
@media (max-width: 768px) {
  body header section.header-top .container .user-account {
    margin-right: 16px;
  }
}
body header section.header-top .container .user-account i {
  padding-left: 5px;
  font-size: 10px;
}
@media (max-width: 768px) {
  body header section.header-top .container .user-account .ul-user-account-mobile {
    height: 100vh;
    transition: height 0.5s ease-in;
  }
}
body header section.header-top .container .user-account ul {
  position: absolute;
  width: 174px;
  padding: 20px 0 20px 0;
  margin: 190px 0 0 -50px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  box-shadow: 0px 0px 8px #00000029;
  border-radius: 5px;
}
@media (min-width: 769px) {
  body header section.header-top .container .user-account ul {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.4s, opacity 0.4s linear;
  }
}
@media (max-width: 768px) {
  body header section.header-top .container .user-account ul {
    position: fixed;
    width: 100%;
    height: 0;
    overflow: hidden;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    z-index: 1;
    transition: height 0.5s ease-in;
    background-color: #f1f1f1;
  }
}
body header section.header-top .container .user-account ul .close-mobile {
  position: absolute;
  width: 100%;
  padding: 20px;
  top: 0;
  z-index: 1;
  background-color: #cccccc;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  color: #999999;
  text-align: center;
}
@media (min-width: 769px) {
  body header section.header-top .container .user-account ul .close-mobile {
    display: none;
  }
}
body header section.header-top .container .user-account ul .up {
  width: 8px;
  height: 8px;
  position: absolute;
  top: -5px;
  right: 30px;
  transform: rotate(45deg);
  border-top: 1px solid #00000015;
  border-left: 1px solid #00000015;
  background-color: #ffffff;
}
body header section.header-top .container .user-account ul li {
  padding: 10px;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  color: #000000;
  font-size: 0.938em;
}
@media (min-width: 769px) {
  body header section.header-top .container .user-account ul li:hover {
    font-family: "Open Sans", sans-serif;
    font-weight: 700;
  }
}
body header section.header-top .container .cart {
  width: 28px;
  height: 28px;
  margin-top: 15px;
  background: url(/assets/img/cart-bag.png);
  background-repeat: no-repeat;
}
body header section.header-top .container .cart .amount-items {
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 12px;
  margin-top: 10px;
  background-color: #000000;
  border-radius: 50%;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #ffffff;
  font-size: 0.625em;
}
@media (max-width: 425px) {
  body header section.header-top .container .cart .amount-items {
    right: -10px;
  }
}